//------


.blog-home__text {
    display: block;
    padding: 52px 0;
    font-size:$text--s;
    font-family: $font--raleway;
}

.blog-home__content {
    display: flex;
    flex-direction: column-reverse;
    margin-bottom: 40px;
    
    @media only screen and (min-width:768px){
        flex-direction: row;
    }
}
 
.blog-contnt--left {

    @media only screen and (min-width:768px){
        flex: 1 auto;
        padding-right:40px;
        margin-top: 36px;
        max-width: calc(100% - 124px);
    }
    .content-62 {
        color: var(--text-color);
        background: var(--background-color);
    }
    .root-61 {
        &:before {
            background-color: var(--accent-color);;
        }
    }

}

.blog-contnt--right { 
    margin-bottom: 52px;
    @media only screen and (min-width:768px) {
        margin-bottom: 0;
    }
    button.ms-Button {
        padding-left: 10px;
    }
}

.nav__header {
    padding-bottom: 20px;
}


.ms-Nav-group {
    min-width: 124px;
}

.nav-category {
    .ms-Nav-chevronButton {
        color: var(--text-color);
        background-color: var(--accent-color);
    }
    .ms-Nav-link
     {
        color: var(--text-color);
    }
    .ms-Nav-compositeLink:hover .link-110 {
        background-color: var(--accent-color);
    }
}